import React from 'react';
import Cancel from '../../Buttons/Cancel/Cancel';
import Save from '../../Buttons/Save/Save';
import Modal from '../Modal';
import '../Modals.css'

const EditCardModal = ({ onClickCancel, onClickSave, card, handleOnChangeQuestion, handleOnChangeAnswer }) => {
    const background = "edit-modal-background";
    const title = "Edit Card";
    const body = (
        <div className="df f-d-column f-f-g-1 f-j-c-center f3">
            <p className="mh3 mt3">Front:</p>
            <strong 
                id="question"
                className="ma4 edit-modal-background"
                suppressContentEditableWarning="true" 
                contentEditable="true"
                onInput={handleOnChangeQuestion}>{card.question}</strong>
            <hr className="mh4" />
            <p className="mh3 mt3">Back:</p>
            <strong 
                id="answer" 
                className="ma4 edit-modal-background" 
                suppressContentEditableWarning="true" 
                contentEditable="true"
                onInput={handleOnChangeAnswer}>{card.answer}</strong>
        </div>
    );
    const buttons = [
        <Cancel onClickCancel={onClickCancel} />,
        <Save onClickSave={onClickSave} />
    ];

    return (
        <Modal
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default EditCardModal;