import React from 'react';
import Cancel from '../../Buttons/Cancel/Cancel';
import Save from '../../Buttons/Save/Save';
import Modal from '../Modal'
import '../Modals.css'

const AddCardModal = ({ onClickCancel, onClickSave, handleOnChangeQuestion, handleOnChangeAnswer }) => {
    const background = "add-modal-background";
    const title = "Add Card";
    const body = (
        <div className="max-dimensions df f-d-column f-j-c-start f3">
            <p className="mh3 mt3">Front:</p>
            <strong 
                id="question" 
                className="ma4 add-modal-background" 
                contentEditable="true"
                onInput={handleOnChangeQuestion}></strong> 
            <hr className="mh4" />
            <p className="mh3 mt3">Back:</p>
            <strong 
                id="answer" 
                className="ma4 add-modal-background" 
                contentEditable="true"
                onInput={handleOnChangeAnswer}></strong>
        </div>
    );
    const buttons = [
            <Cancel onClickCancel={onClickCancel} />,
            <Save onClickSave={onClickSave} />
    ];

    return (
        <Modal 
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default AddCardModal;