import React, { Component } from 'react';
import Navigation from './Navigation/Navigation'
import './App.css';
import SideMenu from './SideMenu/SideMenu'
import Home from './Screens/Home/Home'
import Decks from './Screens/Decks/Decks'
import Cards from './Screens/Cards/Cards'
import Stats from './Screens/Stats/Stats'
import About from './Screens/About/About'
import ViewCardModal from './Modals/ViewCardModal/ViewCardModal';
import EditDeckModal from './Modals/EditDeckModal/EditDeckModal';
import EditCardModal from './Modals/EditCardModal/EditCardModal';
import DeleteDeckModal from './Modals/DeleteDeckModal/DeleteDeckModal';
import DeleteCardModal from './Modals/DeleteCardModal/DeleteCardModal';
import AddDeckModal from './Modals/AddDeckModal/AddDeckModal';
import AddCardModal from './Modals/AddCardModal/AddCardModal';
import decks from './data'

const helpers = {
  selectOption: function(i) {
    console.log("helpers.selectOption");
    document.querySelectorAll('.menu-item').forEach((item, index) => {
      item.classList.remove('menu-item-clicked')
      if (index === i) {
          item.classList.add('menu-item-clicked')
      }
    })
  }
}

class App extends Component {
  constructor(props) {
    console.log("constructor");
    super(props)
    this.state = {
      menuOption: "home",
      currentPage: "home",
      displayModal: "",
      isModalDisplayed: false,
      decks: decks,
      currentDeckId: null,
      currentCardId: null
    }
  }

  componentDidMount() {
    console.log("compoonentDidMount");
    this.selectItem();
  }

  selectItem = () => {
    console.log("selectItem");
    switch(this.state.menuOption) {
      case "home":
        helpers.selectOption(0)
        break;
      case "decks":
        helpers.selectOption(1)
        break;
      case "stats":
        helpers.selectOption(2)
        break;
      case "about":
        helpers.selectOption(3)
        break;
      default:
        helpers.selectOption(-1)
        break;   
    }
  }
  
  setSelectedItem = (option) => {
    console.log("setSelectedItem");
    if (!this.state.isModalDisplayed) {
      this.setState({
        menuOption: option,
        currentPage: option,
        currentCardId: null,
        currentDeckId: null
      }, ()=>{this.selectItem()});    
    }
  }

  onClickAdd = () => {
    console.log("onClickAdd");
    (this.state.currentPage === "decks") ? 
      this.setState({
        displayModal: "addDeck",
        isModalDisplayed: true,
        currentDeck: {
          id: null,
          name: "",
          cards: []
        }
      }) :
      this.setState({
        displayModal: "addCard",
        isModalDisplayed: true,
        currentCard: { 
          id: null,
          question: "",
          answer: "",
          dueDate: new Date().toISOString(),
          oldCard: false,
          level: 1,
        }
      }, () => console.log(this.state))
  }

  onClickDelete = (e) => {
    console.log("onClickDelete");
    (this.state.currentPage === "decks") ?
      this.openDeckModal("deleteDeck", e) : 
      this.openCardModal("deleteCard", e) 
  }

  onClickEdit = (e) => {
    console.log("onClickEdit");
    (this.state.currentPage === "decks") ?
      this.openDeckModal("editDeck", e) : 
      this.openCardModal("editCard", e) 

  }

  onClickView = (e) => {
    console.log("onClickView");
    console.log(e.target);
    (this.state.currentPage === "decks") ?
      this.setState({
        currentPage: "cards",
        currentDeckId: e.target.id, 
        currentDeck: this.state.decks[e.target.id] 
      }, () => console.log(this.state)) :
      this.openCardModal('viewCard', e)
  }

  onClickCancel = () => {
    console.log("onClickCancel");
    (this.state.currentPage === "decks") ?
      this.backToDecksPage() : 
      this.backToCardsPage();
  }

  onClickConfirm = () => {
    console.log("onClickConfirm");
    if (this.state.currentPage === "decks") {
      this.deleteDeck()
      this.backToDecksPage();
    } else if (this.state.currentPage === "cards") {
      this.deleteCard()
      this.backToCardsPage();
    }
  }

  onClickSave = () => {
    console.log("onClickSave");
    switch (this.state.displayModal) {
      case "editDeck":
        this.updateDeck();
        this.backToDecksPage();
        break;
      case "editCard":
        this.updateCard();
        this.backToCardsPage();
        break;
      case "addDeck":
        this.createDeck();
        this.backToDecksPage();
        break;
      case "addCard":
        this.createCard();
        this.backToCardsPage();
        break;
      default:
        break;
    }
  }

  onClickStart = () => {
    console.log("onClickStart");
    this.setSelectedItem('');
  }
  
  backToDecksPage = () => {
    console.log("backtoDeksPage");
    this.setState({
      displayModal: "",
      isModalDisplayed: false,
      currentDeckId: null,
      currentDeck: {}
    }, () => console.log(this.state))
  }

  backToCardsPage = () => {
    console.log("backToCardsPage");
    this.setState({
      displayModal: "",
      isModalDisplayed: false,
      currentCardId: null, 
      currentCard: {}
    }, () => console.log(this.state))
  }

  openCardModal = (modal, event) => {
    console.log("openCardModal");
    this.setState({
      displayModal: modal,
      isModalDisplayed: true,
      currentCardId: event.target.id,
      currentCard: this.state.decks[this.state.currentDeckId].cards[event.target.id]                 
    }, () => console.log(this.state))
  }

  handleOnChangeName = (e) => {
    console.log("handleOnChangeName");
    const currentDeck = JSON.parse(JSON.stringify(this.state.currentDeck))
    currentDeck.name = e.target.textContent;
    console.log("currentDeck", currentDeck)
    console.log("state", this.state)
    this.setState({ currentDeck }, () => console.log(this.state))
  }

  handleOnChangeQuestion = (e) => {
    console.log("handleOnChangeQuestion");
    const currentCard = JSON.parse(JSON.stringify(this.state.currentCard))
    currentCard.question = e.target.textContent;
    this.setState({ currentCard }, () => console.log(this.state))
  }

  handleOnChangeAnswer = (e) => {
    console.log("handleOnChangeAnswer");
    const currentCard = JSON.parse(JSON.stringify(this.state.currentCard))
    currentCard.answer = e.target.textContent;
    this.setState({ currentCard }, () => console.log(this.state))
  }

  openDeckModal = (modal, event) => {
    console.log("openDeckModal");
    this.setState({
      displayModal: modal,
      isModalDisplayed: true,
      currentDeckId: event.target.id,
      currentDeck: this.state.decks[event.target.id]
    }, () => console.log(this.state))
  }

  createDeck = () => {
    console.log("createDeck");
    const decks = JSON.parse(JSON.stringify(this.state.decks));
    decks.push(this.state.currentDeck)
    this.setState({ decks })
  }

  createCard = () => {
    console.log("createCard");
    const decks = JSON.parse(JSON.stringify(this.state.decks));
    decks[this.state.currentDeckId].cards.push(this.state.currentCard)
    this.setState({ decks })
  }
  updateDeck = () => {
    console.log("updateDeck");
    const decks = JSON.parse(JSON.stringify(this.state.decks));
    decks.splice(this.state.currentDeckId, 1, this.state.currentDeck)
    this.setState({ decks })
  }

  updateCard = () => {
    console.log("updateCard");
    const decks = JSON.parse(JSON.stringify(this.state.decks));
    decks[this.state.currentDeckId].cards.splice(this.state.currentCardId, 1, this.state.currentCard)
    this.setState({ decks })
  }

  deleteDeck = () => {
    console.log("deleteDeck");
    let decks = this.state.decks;
    decks.splice(this.state.currentDeckId, 1);
    this.setState({ decks })
  }

  deleteCard = () => {
    console.log("deleteCard");
    let decks = this.state.decks;
    decks[this.state.currentDeckId].cards.splice(this.state.currentCardId, 1);
    this.setState({ decks })
  }

  render() {
    console.log("render");
    const { menuOption, displayModal, currentPage, decks, currentDeckId, currentCardId } = this.state;
    // const oldDecks = decks.filter(deck => deck.cards.some(card => !card.oldCard));
    
    const page = (menuOption === 'home') ? 
      <Home onClickStart={this.onClickStart}/> :
      ((menuOption === 'decks') ? 
        ((currentPage === "decks") ? 
          <Decks 
            onClickAdd={this.onClickAdd} 
            onClickView={this.onClickView} 
            onClickEdit={this.onClickEdit} 
            onClickDelete={this.onClickDelete} 
            decks={decks}/> :
          ((currentPage === "cards") ?
            <Cards 
              onClickAdd={this.onClickAdd} 
              onClickView={this.onClickView} 
              onClickEdit={this.onClickEdit} 
              onClickDelete={this.onClickDelete}
              deck={decks[currentDeckId]} /> : 
            null)) :
        ((menuOption === 'stats') ? <Stats /> :
          ((menuOption === 'about') ? <About /> : null)))

    const modal = (displayModal === "viewCard") ? <ViewCardModal onClickCancel={this.onClickCancel} deckName={decks[currentDeckId].name} card={decks[currentDeckId].cards[currentCardId]}/> :
      ((displayModal === "editCard") ? <EditCardModal onClickCancel={this.onClickCancel} onClickSave={this.onClickSave} card={decks[currentDeckId].cards[currentCardId]} handleOnChangeQuestion={this.handleOnChangeQuestion} handleOnChangeAnswer={this.handleOnChangeAnswer} /> :
        ((displayModal === "editDeck") ? <EditDeckModal onClickCancel={this.onClickCancel} onClickSave={this.onClickSave} deck={decks[currentDeckId]} handleOnChangeName={this.handleOnChangeName} /> :
          ((displayModal === "deleteCard") ? <DeleteCardModal onClickCancel={this.onClickCancel} onClickConfirm={this.onClickConfirm} /> :
            ((displayModal === "deleteDeck") ? <DeleteDeckModal onClickCancel={this.onClickCancel} onClickConfirm={this.onClickConfirm} /> :
              ((displayModal === "addCard") ? <AddCardModal onClickCancel={this.onClickCancel} onClickSave={this.onClickSave} handleOnChangeQuestion={this.handleOnChangeQuestion} handleOnChangeAnswer={this.handleOnChangeAnswer} /> :
                ((displayModal === "addDeck") ? <AddDeckModal onClickCancel={this.onClickCancel} onClickSave={this.onClickSave} handleOnChangeName={this.handleOnChangeName} /> : null))))))

    return (
      <div className="df f-d-column">
        <Navigation />
        <div className="f-f-g-1 df f-a-i-stretch">
          <SideMenu setSelectedItem={this.setSelectedItem} selectItem={this.selectItem} onClickStart={this.onClickStart} />
          <div className="f-f-g-1 relative">
            {page}
            {modal}
          </div>
        </div>
      </div>
    );
  }
}

export default App;