import React from 'react';
import Cancel from '../../Buttons/Cancel/Cancel';
import Save from '../../Buttons/Save/Save';
import Modal from '../Modal';
import '../Modals.css'

const AddDeckModal = ({ onClickCancel, onClickSave, handleOnChangeName }) => {
    const background = "add-modal-background";
    const title = "Add Deck";
    const body = (
        <div className="max-dimensions df f-d-column f-j-c-start f3">
            <div className="f-f-g-1 df f-d-column f-j-c-center">
                <p className="mh3 mt3">Deck Name:</p>
                <strong 
                    id="name" 
                    className="ma4 add-modal-background" 
                    contentEditable="true"
                    onInput={handleOnChangeName}></strong>
            </div>
        </div>
    );
    const buttons = [
        <Cancel onClickCancel={onClickCancel} />,
        <Save onClickSave={onClickSave} />
    ];

    return (
        <Modal 
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default AddDeckModal;